.button_container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

button {
  background-color: #eebbc3;
  cursor: pointer;
  padding-left: 35px;
  border-radius: 5px;
  height: 30px;
  width: 185px;
  border: solid 2.5px #121629;
  font-weight: 600; 
}

.icon {
  position: absolute;
  left: 10px;
  color: #232946;
}